<template>
<div class="content">
    <el-breadcrumb separator="/">
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item>
            <a href="/"></a>
        </el-breadcrumb-item>
    </el-breadcrumb>
    <div>
        <!--  -->
        <div class="seach">
            <div class="seach_top">
                <el-input placeholder="" v-model="input" class="input-with-select" @keyup.enter.native="onSubmitInput">
                    <i slot="prefix" class="el-input__icon el-icon-search" @click="seachuser()"></i>
                </el-input>
                <div class="seach_top_right" @click="option_display()">
                    
                    <i class="el-icon-caret-bottom" :class="[rotate?'fa fa-arrow-down go':'fa fa-arrow-down aa']"></i>
                </div>
            </div>
            <div v-if="optiondisplay" class="seach_bottom">
                <span></span>
                <el-select v-model="value" placeholder="">
                    <el-option v-for="(item, index) in options" :key="index" :label="item.label" :value="item.value"></el-option>
                </el-select>

                <span></span>
                <el-date-picker v-model="value1" type="datetimerange" range-separator="" start-placeholder="" end-placeholder=""></el-date-picker>
                <div class="seach_bottom_btn">
                    <el-button type="primary" plain size="mini" @click="seachuser()"></el-button>
                    <el-button plain size="mini" @click="reset()"></el-button>
                </div>
            </div>
        </div>
        <!--  -->
        <div class="con_lable">
            <div style="padding:10px;display: flex;justify-content: space-between;">
                <div>
                    <el-button type="primary" @click="addUrl">
                        
                        <span class="el-icon-circle-plus-outline"></span>
                    </el-button>
                    <el-button type="primary" @click="setdomainlist"></el-button>
                    <el-button type="primary" @click="onImport">URL</el-button>
                </div>
                <div>
                    <el-button type="primary" @click="exportExcel"></el-button>
                </div>
            </div>

            <!--  -->
            <el-table ref="multipleTable" @selection-change="handleSelectionChange" :data="tableData" tooltip-effect="dark" style="width: 100%" :cell-style="rowClass" :header-cell-style="headClass" :default-sort="{prop: 'date', order: 'descending'}">
                <el-table-column type="selection" width="55">
                </el-table-column>
                <el-table-column prop="url" label=""></el-table-column>
                <el-table-column prop="label" label=""></el-table-column>
                <el-table-column prop="buser_id" label="ID"></el-table-column>
                <el-table-column prop="state" label="" :formatter="formatState"></el-table-column>
                <el-table-column prop="create_time" sortable label="" :formatter="formatTime"></el-table-column>
                <el-table-column prop="statuss" label=""></el-table-column>

                <el-table-column label="" width="300">
                    <template slot-scope="scope">
                        <el-button @click="Configuration(scope.row)" type="text" size="small"></el-button>
                        <el-button type="text" size="small" @click="updatauser(scope.row)"></el-button>
                        <el-button type="text" size="small" @click="onDisable(scope.row)">{{scope.row.state==0?"":""}}</el-button>
                        <el-button type="text" size="small" @click="handleClick(scope.row)"></el-button>
                        <el-button type="text" size="small" @click="deleateuser(scope.row)" style="color:red;"></el-button>
                    </template>
                </el-table-column>
            </el-table>

            <!--  -->
            <div style="margin-top: 20px;display: flex;justify-content: space-between;align-items: center;">
                <div>
                    <el-button @click="enableuser()" type="success"></el-button>
                    <el-button @click="disableuser()" type="warning"></el-button>
                    <el-button @click="deleateuser()" type="danger"></el-button>
                </div>
                <fenye style="float:right;margin:10px 0 0 0;" @handleCurrentChange="handleCurrentChange" @handleSizeChange="handleSizeChange" :currentPage="currentPage" :pagesa="total_cnt"></fenye>
            </div>
        </div>

    </div>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisible" custom-class="customWidth" class="domain_dialog">
        <el-form :model="dynamicValidateForm" ref="dynamicValidateForm">

            <el-form-item label="ID:" :label-width="formLabelWidth" prop="account" :rules="{
      required: true, message: 'ID', trigger: 'blur'
    }">
                <el-input v-model="dynamicValidateForm.buser_id" placeholder="ID" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="URL:" :label-width="formLabelWidth" prop="pwd" placeholder="URL" :rules="{
      required: true, message: '', trigger: 'blur'
    }">
                <el-input v-model="dynamicValidateForm.url" placeholder="" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label=":" :label-width="formLabelWidth" prop="conpwd" :rules="{
      required: true, message: '', trigger: 'blur'
    }">
                <el-input v-model="dynamicValidateForm.url_name" placeholder="" autocomplete="off"></el-input>
            </el-form-item>

            <el-form-item label="" :label-width="formLabelWidth" prop="radio" :rules="{
      required: true, message: '', trigger: 'blur'
    }">
                <el-select v-model="dynamicValidateForm.url_type" placeholder="" style="width:100%;">
                    <el-option v-for="(item, index) in videoArr" :key="index" :label="item.label" :value="item.value"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label=":" :label-width="formLabelWidth" prop="conpwd" :rules="{
      required: true, message: '', trigger: 'blur'
    }">
                <el-input v-model="dynamicValidateForm.label" placeholder="" autocomplete="off"></el-input>
            </el-form-item>

            <el-form-item label="" :label-width="formLabelWidth" prop="radio" :rules="{
      required: true, message: '', trigger: 'blur'
    }">
                <el-select v-model="dynamicValidateForm.label2" placeholder="" style="width:100%;">
                    <el-option v-for="(item, index) in yewu" :key="index" :label="item.label" :value="item.value"></el-option>
                </el-select>
            </el-form-item>

        </el-form>

        <div slot="footer" class="dialog-footer" style="text-align:center;">
            <el-button @click="dialogFormVisibles"> </el-button>
            <el-button type="primary" @click="onSubmitAdd"> </el-button>
        </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="dialog" custom-class="customWidth" width="50%" style="width:100%:background:red;">
        <el-table ref="multipleTable" :data="tableData">
            <el-table-column prop="dominds" label=""></el-table-column>
            <el-table-column prop="camesd" label=""></el-table-column>
            <el-table-column label="">
                <template slot-scope="scope">
                    <el-button @click="handleClicks(scope.row)" type="text" size="small"></el-button>
                </template>
            </el-table-column>
        </el-table>

        <div slot="footer" class="dialog-footer">
            <el-button type="primary" @click="dialog=false"> </el-button>
        </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="dialupdata" custom-class="customWidth" width="900px">
        <p style="text-align: left;margin: 10px 0;"></p>
        <el-steps :active="actives" finish-status="success" align-center>
            <el-step title=""></el-step>
            <el-step title=""></el-step>
            <el-step title=""></el-step>
        </el-steps>

        <div v-if="actives===1">
            <el-table ref="multipleTable" :data="futableData" tooltip-effect="dark" border style="width: 100%" :cell-style="rowClass" :header-cell-style="headClass" @selection-change="handleSelectionChange">
                <el-table-column type="selection" width="55"></el-table-column>
                <el-table-column prop="configuration" label=""></el-table-column>
                <el-table-column prop="nowconfiguration" label="" show-overflow-tooltip></el-table-column>
            </el-table>
            <el-button style="margin-top: 12px;" type="primary" @click="next"></el-button>
            <el-button style="margin-top: 12px;" @click="fureset"></el-button>
        </div>
        <div v-else-if="actives===2">
            <div style="display: flex;justify-content: space-between;align-items: center;margin: 10px 0">
                <span style="font-weight: 600;"></span>
                <div>
                    
                    <span>{{nums}}</span>50
                </div>
                <el-input placeholder="" v-model="fuinput" style=" width:300px" class="input-with-select" @keyup.enter.native="onSubmit"></el-input>
            </div>
            <div>
                <el-table ref="multipleTable" :data="urllist" tooltip-effect="dark" border style="width: 100%" :cell-style="rowClass" :header-cell-style="headClass" @selection-change="handleSelectionChange">
                    <el-table-column type="selection" width="55"></el-table-column>
                    <el-table-column prop="url" label=""></el-table-column>
                </el-table>
            </div>
            <el-button style="margin-top: 12px;" type="primary" @click="last"></el-button>
            <el-button style="margin-top: 12px;" type="primary" @click="next"></el-button>
            <el-button style="margin-top: 12px;" @click="fureset"></el-button>
        </div>
        <!-- <div v-else>
            <el-button style="margin-top: 12px;" type="primary" @click="last"></el-button>
            <el-button style="margin-top: 12px;"  @click="fureset"></el-button>
          </div>-->
    </el-dialog>

</div>
</template>

<script>
import fenye from "@/components/fenye";
import common from '../../comm/js/util'
import {
    query_url,
    add_url,
    delete_url,
    change_state
} from "../../servers/api";
export default {
    data() {
        //

        //
        var validatePass = (rule, value, callback) => {
            if (value === "") {
                callback(new Error(""));
            } else {
                if (this.ruleForm.checkPass !== "") {
                    this.$refs.ruleForm.validateField("checkPass");
                }
                callback();
            }
        };
        var validatePass2 = (rule, value, callback) => {
            if (value === "") {
                callback(new Error(""));
            } else if (value !== this.ruleForm.pass) {
                callback(new Error("!"));
            } else {
                callback();
            }
        };
        return {
            actives: 1,
            nums: 12,
            fuinput: "",
            ruleForm: {
                pass: "",
                checkPass: "",
                account: ""
            },

            futableData: [{
                    configuration: "HOST",
                    nowconfiguration: ""
                },
                {
                    configuration: "",
                    nowconfiguration: ""
                },
                {
                    configuration: "",
                    nowconfiguration: ""
                },
                {
                    configuration: "",
                    nowconfiguration: ""
                },
                {
                    configuration: "Refer",
                    nowconfiguration: ""
                },
                {
                    configuration: "URL",
                    nowconfiguration: ""
                },
                {
                    configuration: "IP/",
                    nowconfiguration: ""
                }
            ],
            //
            rules: {
                pass: [{
                    validator: validatePass,
                    trigger: "blur"
                }],
                checkPass: [{
                    validator: validatePass2,
                    trigger: "blur"
                }]
            },
            input: "", //
            value1: "",
            dialogFormVisible: false,
            dialog: false,
            dialupdata: false,
            dialpwdset: false,
            radio: "1",
            radioes: "1",
            isIndeterminate: "",
            sadioes: 1,
            currentPage: 1,
            pagesize: 10,
            total_cnt: 0,
            dynamicValidateForm: {
                buser_id: "",
                url: "",
                url_name: "",
                url_type: "",
                label: "",
                label2: "",

            },
            details: {},
            uetails: {},
            formLabelWidth: "60px",
            optiondisplay: false,
            rotate: false,
            gridData: [],
            tolpage: 1,
            options: [{
                    value: -1,
                    label: ""
                }, {
                    value: 0,
                    label: ""
                },
                {
                    value: 1,
                    label: ""
                },
                {
                    value: 2,
                    label: ""
                },
                {
                    value: 3,
                    label: ""
                },
                {
                    value: 4,
                    label: ""
                },
                {
                    value: 5,
                    label: ""
                }
            ],
            yewu: [{
                    value: 0,
                    label: ""
                },
                {
                    value: 1,
                    label: ""
                },
                {
                    value: 2,
                    label: ""
                }
            ],
            videoArr: [{
                    value: 0,
                    label: "MP4"
                },
                {
                    value: 1,
                    label: "hls"
                },
                {
                    value: 2,
                    label: "flv"
                }
            ],
            value: -1,

            tableData: [],
            urllist: [{
                    url: "http://www.xxx.cn.abc"
                },
                {
                    url: "http://www.xxx.cn.abc"
                },
                {
                    url: "http://www.xxx.cn.abc"
                },
                {
                    url: "http://www.xxx.cn.abc"
                },
                {
                    url: "http://www.xxx.cn.abc"
                },
                {
                    url: "http://www.xxx.cn.abc"
                }
            ],
            multipleSelection: []
        };
    },
    components: {
        fenye
    },
    created() {},
    mounted() {
        this.queryUrlList();
    },
    methods: {
        handleSelectionChange(val) {
            this.multipleSelection = val;
            console.log(this.multipleSelection)
        },
        //
        onImport() {
            this.$router.push({
                path: "/upload"
            })

        },

        //
        //

        exportExcel() {
            require.ensure([], () => {
                const {
                    export_json_to_excel
                } = require("../../excel/Export2Excel");
                const tHeader = [

                    "",
                    "",
                    "ID",
                    "",
                    "",
                    "",

                ];
                // Excel
                const filterVal = [

                    "url",
                    "label",
                    "buser_id",
                    "state",
                    "create_time",
                    "statuss",

                ];
                // indexnickNamenametableData
                const list = this.tableData; //datatableDatalist
                const data = this.formatJson(filterVal, list);
                export_json_to_excel(tHeader, data, "");
            });
        },
        formatJson(filterVal, jsonData) {
            return jsonData.map(v => filterVal.map(j => v[j]));
        },
        // 
        onSubmitInput() {
            onSubmitInput
        },
        //
        formatState(rows) {
            if (rows.state == 0) {
                return ""
            } else {
                return ""
            }
            console.log(rows)
            //return jsonData.map(v => filterVal.map(j => v[j]));
        },
        formatTime(rows) {
            let tempTime = (rows.create_time) * 1000
            return this.common.getTimes(tempTime)

        },
        //
        next() {
            if (this.actives == 2) {
                const confirmText = [
                    "",
                    ""
                ];
                const newDatas = [];
                const h = this.$createElement;
                for (const i in confirmText) {
                    newDatas.push(h("p", null, confirmText[i]));
                }

                this.$confirm("", {
                        title: "",
                        message: h("div", null, newDatas),
                        showCancelButton: true,
                        confirmButtonText: "",
                        cancelButtonText: "",
                        type: "warning"
                    })
                    .then(() => {
                        this.dialupdata = false;
                        this.actives = 1;
                        // 
                        this.$message({
                            type: "success",
                            message: "!"
                        });
                    })
                    .catch(() => {
                        this.dialupdata = false;
                        this.actives = 1;
                        this.$message({
                            type: "info",
                            message: ""
                        });
                    });
            }
            if (this.actives++ > 2) {
                if (this.active++ > 2) this.active = 0;
                return false;
            }
        },
        //
        last() {
            if (this.actives-- < 1) {
                // this.actives = 0;
                return false;
            }
        },
        //
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        //
        fureset() {
            this.actives = 1;
            this.dialupdata = false;
        },
        //
        Configuration() {
            this.$router.push({
                path: "/back_source"
            })
        },
        new_btn() {

        },
        //URL
        onSubmitAdd() {
            this.dialogFormVisible = false
            let param = new Object
            param = this.dynamicValidateForm
            console.log(param)
            add_url(param).then(res => {

                console.log(res)

                if (res.status == 0) {
                    this.$message({
                        type: "success",
                        message: ""
                    });
                    this.queryUrlList()
                }

            }).catch(error => {
                console.log(error)
            })

        },
        //-URL
        removeDomain(item) {
            var index = this.dynamicValidateForm.domains.indexOf(item);
            if (index !== -1) {
                this.dynamicValidateForm.domains.splice(index, 1);
            }
        },
        //-URL
        addDomain() {
            this.dynamicValidateForm.domains.push({
                value: "",
                key: Date.now()
            });
        },
        //-
        resetForm(formName) {
            console.log(formName);
            this.$refs[formName].resetFields();
        },
        //
        setdomainlist() {
            let tempUrlArr = []
            tempUrlArr = this.tableData
            console.log(this.tableData)
            localStorage.setItem('tempUrlArr', JSON.stringify(tempUrlArr))

            this.$router.push({
                path: "/batch_management"
            })
        },
        //URL
        queryUrlList() {

            let params = new Object();
            params.page = this.currentPage - 1
            params.buser_id = ""
            params.url = this.input
            if (this.value == "") {
                params.state = -1
            } else {
                params.state = this.value
            }
            if (this.value1) {
                params.end_time = this.value1[1].getTime() / 1000
                params.start_time = this.value1[0].getTime() / 1000
            } else {

            }
            //params.state=0
            // params.end_time=123456
            //  params.start_time=23456
            query_url(params)
                .then(res => {
                    console.log(res);
                    if (res.status == 0) {

                        this.tableData = res.data.result
                        this.total_cnt = res.data.total
                        console.log(this.tableData)
                    } else {

                    }
                })
                .catch(err => {
                    console.log(err);
                });
        },
        //
        handleCurrentChange(pages) {
            console.log(pages);
            this.currentPage = pages;
            this.queryUrlList()
            //this.getdata();
        },
        //
        handleSizeChange(pagetol) {
            console.log(pagetol);
            this.currentPage = pagetol;
            //this.getdata();
        },
        //
        onSubmit() {
            console.log("enter");
        },
        //
        Configuration(val) {
            console.log(val)
            let tempUrl = val.url
            localStorage.setItem('tempUrl', tempUrl)
            console.log(tempUrl)
            this.$router.push({
                path: "/back_source",
            })
        },
        //
        option_display() {
            console.log("******");
            this.optiondisplay = !this.optiondisplay;
            this.rotate = !this.rotate;
        },
        //
        seachuser() {
            this.optiondisplay = false;
            // this.value = "";
            // this.input = "";
            this.queryUrlList()
            //this.value1 = "";

        },
        //
        reset() {
            this.value = "";
            this.value1 = null;
            this.input = "";
            this.queryUrlList()
            console.log("");
        },
        //
        addUrl() {
            this.$router.push({
                path: "/add_url"
            })
            this.dialogFormVisible = true;
        },
        //
        dialogFormVisibles() {
            console.log("");
            this.radio = "1";
            this.dynamicValidateForm.account = "";
            this.dynamicValidateForm.nickname = "";
            this.dynamicValidateForm.pwd = "";
            this.dynamicValidateForm.conpwd = "";
            this.dynamicValidateForm.actualname = "";
            this.dynamicValidateForm.tel = "";
            this.dialogFormVisible = false;
        },
        //
        dialogFormVisiblea() {
            console.log("");
            console.log(
                this.dynamicValidateForm.name,
                this.dynamicValidateForm.region
            );
            this.dynamicValidateForm.name = "";
            this.dynamicValidateForm.region = "";
            this.dialogFormVisible = false;
        },
        //
        handleClick(row) {
            console.log(row);
            if (row.radio == "") {
                this.sadioes = "1";
            } else {
                this.sadioes = "2";
            }
            this.details = row;
           // this.dialog = true;
        },
        //
        updatauser(row) {
            console.log(row);
            if (row.radio == "") {
                this.radioes = "1";
            } else {
                this.radioes = "2";
            }
            this.uetails = row;
            this.$router.push({
                path:"/copy"
            })
           // this.dialupdata = true;
        },
        //
        updataa() {
            this.uetails = {};
            this.dialupdata = false;
        },
        //
        updatab() {
            this.uetails = {};
            this.dialupdata = false;
        },
        //
        onDisable(row) {
            console.log(row);
            if (row.state == 0) {
                //
                let param = {
                    url: row.url,
                    state: 1
                }
                change_state(param).then(res => {
                    console.log(res)
                    if (res.status == 0) {
                        this.$message({
                            message: "",
                            type: "success"
                        });
                    }
                    this.queryUrlList()

                }).catch(error => {
                    console.log(error)
                })
            } else if (row.state == 1) {
                //
                let param = {
                    url: row.url,
                    state: 0
                }
                change_state(param).then(res => {
                    console.log(res)
                    if (res.status == 0) {
                        this.$message({
                            message: "",
                            type: "success"
                        });
                    }
                    this.queryUrlList()

                }).catch(error => {
                    console.log(error)
                })
            }
            // this.ruleForm.account = row.account;
            // this.dialpwdset = true;
        },
        submitForm(formName) {
            this.$refs[formName].validate(valid => {
                if (valid) {
                    this.$message({
                        message: "",
                        type: "success"
                    });
                    this.dialpwdset = false;
                } else {
                    console.log("error submit!!");
                    return false;
                }
            });
        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
            this.dialpwdset = false;
        },
        // 
        disableuser() {
            this.$confirm(", ?", "", {
                    confirmButtonText: "",
                    cancelButtonText: "",
                    type: "warning"
                })
                .then(() => {
                    this.$message({
                        type: "success",
                        message: "!"
                    });
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: ""
                    });
                });
        },
        //
        enableuser() {
            console.log(this.multipleSelection)
            let tempArr = this.multipleSelection
            let tempArr1 = []
            let tempArr2 = []
            for (var i = 0; i < tempArr.length; i++) {
                tempArr1[0] = tempArr[i].url
                tempArr1[1] = 1
                tempArr2.push(tempArr1)
            }

            let param = new Object()
            param.data_count = tempArr.length
            param.data_array = tempArr2
            change_state(param).then(res => {
                console.log(res)
                if (res.status == 0) {
                    this.$message({
                        type: "success",
                        message: "!"
                    });
                    this.queryUrlList()
                }
            }).catch(error => {
                console.logA(error)
            })

        },
        //
        disableuser() {
            console.log(this.multipleSelection)
            let tempArr = this.multipleSelection
            let tempArr1 = []
            let tempArr2 = []
            for (var i = 0; i < tempArr.length; i++) {
                tempArr1[0] = tempArr[i].url
                tempArr1[1] = 0
                tempArr2.push(tempArr1)
            }

            let param = new Object()
            param.data_count = tempArr.length
            param.data_array = tempArr2
            change_state(param).then(res => {
                console.log(res)
                if (res.status == 0) {
                    this.$message({
                        type: "success",
                        message: "!"
                    });
                    this.queryUrlList()
                }
            }).catch(error => {
                console.logA(error)
            })

        },

        // 
        deleateuser(rows) {
            console.log(this.multipleSelection)
            let tempArr = this.multipleSelection
            let tempArr1 = []
            let tempArr2 = []
            for (var i = 0; i < tempArr.length; i++) {
                tempArr1.push(tempArr[i].url)
            
            }
            let param = new Object()
            param.data_count = tempArr.length
            param.data_array = tempArr1
            this.$confirm(", ?", "", {
                    confirmButtonText: "",
                    cancelButtonText: "",
                    type: "warning"
                })
                .then(() => {
                    delete_url(param).then(res => {
                        console.log(res)
                        if (res.status == 0) {
                            this.$message({
                                type: "success",
                                message: "!"
                            });
                            this.queryUrlList()
                        }
                    }).catch(error => {

                    })

                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: ""
                    });
                });
        }, // 
        headClass() {
            return "text-align: center;background:#eef1f6;";
        },
        // 
        rowClass() {
            return "text-align: center;";
        }
    }
};
</script>

