<template>
<div class="content">
    <el-breadcrumb separator="/">
        <el-breadcrumb-item> SDK</el-breadcrumb-item>
        <!-- <el-breadcrumb-item>
        <a href="/"></a>
      </el-breadcrumb-item> -->
    </el-breadcrumb>
    <div>
        <!--  -->
        <div class="seach">
            <div class="seach_top">
                <el-input placeholder="" v-model="input" class="input-with-select" @keyup.enter.native="onSubmit">
                    <i slot="prefix" class="el-input__icon el-icon-search" @click="seachuser()"></i>
                </el-input>
                <div class="seach_top_right" @click="option_display()">
                    
                    <i class="el-icon-caret-bottom" :class="[rotate?'fa fa-arrow-down go':'fa fa-arrow-down aa']"></i>
                </div>
            </div>
            <div v-if="optiondisplay" class="seach_bottom">
                <span></span>
                <el-select v-model="valueActive" placeholder="">
                    <el-option v-for="(item, index) in options" :key="index" :label="item" :value="item"></el-option>
                </el-select>
                <span></span>
                <el-date-picker v-model="valueTime" type="datetimerange" range-separator="" start-placeholder="" end-placeholder=""></el-date-picker>
                <div class="seach_bottom_btn">
                    <el-button type="primary" plain size="mini" @click="seachuser()" style="margin-left:37px;"></el-button>
                    <el-button plain size="mini" @click="reset()"></el-button>
                </div>
            </div>
        </div>
        <!--  -->
        <div class="con_lable">
            <div style="text-align: left;margin-bottom:20px;margin-top:20px;">
                <el-button type="primary" @click="addSdk()"></el-button>
            </div>

            <!--  -->
            <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" :cell-style="rowClass" :header-cell-style="headClass" :default-sort="{prop: 'date', order: 'descending'}" @selection-change="handleSelectionChange">
                <el-table-column prop="url" label=""></el-table-column>
                <el-table-column prop="type" label=""></el-table-column>
                <el-table-column prop="content" label=""></el-table-column>
                <el-table-column prop="version" label=""></el-table-column>
                <el-table-column prop="time_create" label=""></el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <div>
                            <el-button @click="handleEdit(scope.row)" type="text" size="small"></el-button>
                        </div>
                    </template>
                </el-table-column>
            </el-table>
            <!--  -->
            <el-dialog title="" :visible.sync="dialog" custom-class="customWidth" width="50%">
                <div class="add-sdk">
                    <div class="item" style="align-items: flex-start;">
                        <div class="item_l"></div>
                        <div class="item-r" style="position: relative;">
                            <el-button class="choose-file" size="mini"></el-button>
                            <input id="f" class="choose-input" type="file" name="file">
                            <el-button type="primary" class="onchoose-file" @click="upFile()" :disabled="disableStatus"></el-button>
                             <span id="per">0</span>%
                            <div style="width: 400px;height: 16px;background-color: #999;margin-top:10px;">
                                <div style="width: 0%;height: 16px;background-color: #67c23a" id="loading"></div>
                            </div>
                            <div id="result" style="margin-top:10px;"></div>
                        </div>
                    </div>
                    <div class="item">
                        <div class="item_l"></div>
                        <div class="item-r">
                            <el-select v-model="valueActive2" placeholder="" style="width:250px;">
                                <el-option v-for="(item, index) in optionsActive2" :key="index" :label="item" :value="item"></el-option>
                            </el-select>
                        </div>
                    </div>
                    <div class="item">
                        <div class="item_l"></div>
                        <div class="item-r">
                            <el-checkbox-group v-model="checkList">
                                <el-checkbox label="mp4"></el-checkbox>
                                <el-checkbox label="hls"></el-checkbox>
                                <el-checkbox label="flv"></el-checkbox>
                            </el-checkbox-group>
                        </div>
                    </div>
                    <div class="item">
                        <div class="item_l"></div>
                        <div class="item-r">
                            <el-input placeholder="" v-model="versionInput" style="width:250px;"></el-input>
                        </div>
                    </div>
                </div>
                <div slot="footer" class="dialog-footer" style="text-align: center;">
                    <el-button type="primary" @click="onSubmitUpload"></el-button>
                    <el-button type="primary" @click="dialog=false"></el-button>
                </div>
            </el-dialog>
            <!--  -->
            <el-dialog title="SDK" :visible.sync="editDialog" custom-class="customWidth" width="50%">
                <div class="add-sdk">
                    <div class="item">
                        <div class="item_l"></div>
                        <div class="item-r" style="position: relative;">
                            <el-button class="choose-file" size="mini"></el-button>
                            <input id="f" class="choose-input" type="file" name="file">
                            <el-button type="primary" class="onchoose-file" @click="upFile()" :disabled="disableStatus"></el-button>
                             <span id="per">0</span>%
                            <div style="width: 400px;height: 16px;background-color: #999;margin-top:10px;">
                                <div style="width: 0%;height: 16px;background-color: #67c23a" id="loading"></div>
                            </div>
                            <div id="result" style="margin-top:10px;"></div>
                        </div>
                    </div>
                    <div class="item">
                        <div class="item_l"></div>
                        <div class="item-r">
                            <el-select v-model="value11" placeholder="">
                                <el-option v-for="(item, index) in options1" :key="index" :label="item" :value="item"></el-option>
                            </el-select>
                        </div>
                    </div>
                    <div class="item">
                        <div class="item_l"></div>
                        <div class="item-r">
                            <el-checkbox-group v-model="checkList11">
                                <el-checkbox label="mp4"></el-checkbox>
                                <el-checkbox label="hls"></el-checkbox>
                                <el-checkbox label="flv"></el-checkbox>
                            </el-checkbox-group>
                        </div>
                    </div>
                    <div class="item">
                        <div class="item_l"></div>
                        <div class="item-r">
                            <el-input v-model="versionInput1" placeholder=""></el-input>
                        </div>
                    </div>
                </div>
                <div slot="footer" class="dialog-footer" style="text-align: center;">
                    <el-button type="primary" @click="onEdit"></el-button>
                    <el-button type="primary" @click="editDialog=false"></el-button>
                </div>
            </el-dialog>

            <!--  -->
            <div style="margin-top: 20px;display: flex;justify-content: space-between;align-items: center;">
                <fenye style="float:right;margin:10px 0 0 0;" @handleCurrentChange="handleCurrentChange" @handleSizeChange="handleSizeChange" :currentPage="currentPage" :pagesa="total_cnt"></fenye>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import fenye from "@/components/fenye";
import {
    userlist,
    sdklist,
    savesdk,
    editsdk
} from "../../servers/api";
export default {
    data() {
        return {
            input: "", //
            value1: "IOS",
            value11: "",
            dialog: false,
            editDialog: false,
            radio: "1",
            radioes: "1",
            checkList: ['mp4'],
            checkList1: [],
            checkList11: [],
            sadioes: 1,
            pagesize: 10,
            valueTime: "",
            total_cnt: 1,
            form: {
                account: "",
                nickname: "",
                pwd: "",
                conpwd: "",
                actualname: "",
                tel: "",
                delivery: false,
                type: [],
                resource: "",
                desc: ""
            },
            details: {},

            formLabelWidth: "60px",
            optiondisplay: false,
            rotate: false,
            disableStatus: false,

            tolpage: 1,
            input1: "",

            value: "",
            pager: {
                count: 0,
                page: 1,
                rows: 100
            },
            valueActive:"",
            valueActive2:"andriod",
            tableData: [],
            options: ["","andriod", "ios"],
            options1: ["andriod", "ios"],
            optionsActive2: ["andriod", "ios"],

            sdkUrl: "",
            versionInput1: "",
            versionInput: "",
            currentPage: 1,
            total_cnt: 0,
            sdkId: 0,

        };
    },
    components: {
        fenye
    },
    created() {},
    mounted() {
        this.querySdkList()
        // this.getuserlist();
    },
    methods: {
        //SDK
        querySdkList() {
            let startTime=""
            let entTime=""
                 if (!this.valueTime) {
               startTime = ""
               entTime = ""

            } else {
                if (this.valueTime[0] == undefined) {
                    startTime = ""
                } else {
                   startTime = this.valueTime[0].getTime() / 1000
                }
                if (this.valueTime[1] == undefined) {
                    entTime = ""
                } else {
                    entTime = this.valueTime[1].getTime() / 1000
                }

            }
     
            let param = {
               
                "page": this.currentPage - 1,
                "search": this.input,
                "type": this.valueActive==""?"":this.valueActive,
                "start_ts": startTime,
                "end_ts": entTime
            }
            sdklist(param).then(res => {
                console.log(res)
                if (res.status == 0) {
                    let temp=res.result.cols
                    for(var i=0;i<temp.length;i++){
                        temp[i].time_create=this.common.getTimes(temp[i].time_create*1000)
                    }
                    this.tableData = res.result.cols
                    if (res.result.page == 0) {
                        this.total_cnt = res.result.cols.length * (res.result.page + 1) + res.result.les_count

                    } else {

                        this.total_cnt = res.result.cols.length + (10 * (res.result.page)) + res.result.les_count
                    }
                }
            }).catch(error => {
                console.log(error)

            })

        },

        //
        addSdk() {
            this.dialog = true
        },
        //
        handleEdit(rows) {
            this.editDialog = true
            console.log(rows)
            this.sdkUrl = rows.url
            let tempArr = rows.content
            this.checkList11 = tempArr.split(",")
            this.versionInput1 = rows.version
            this.value11 = rows.type
            this.sdkId = rows.id
        },
        //
        onEdit() {
            this.editDialog = false

            let param = {
                id: this.sdkId,
                url: this.sdkUrl,
                type: this.value11,
                content: this.checkList11.join(","),
                version: this.versionInput1,
                description: "test"
            }
            editsdk(param).then(res => {
                console.log(res)
                if (res.status == 0) {
                    this.$message({
                        message: "",
                        type: "success"
                    })
                    this.querySdkList()
                }
            }).catch(error => {

            })
        },

        //
        handleCurrentChange(pages) {
            this.currentPage = pages;
            this.querySdkList()
            //this.getdata();
        },
        //
        handleSizeChange(pagetol) {

            //this.getdata();
        },
        //
        onSubmit() {
            console.log("enter");
        },
        //
        option_display() {
            console.log("******");
            this.optiondisplay = !this.optiondisplay;
            this.rotate = !this.rotate;
        },
        //
        seachuser() {
       
            this.querySdkList()
        },
        //
        reset() {
            this.value = "";
            this.input = "";
            console.log("");
        },
        //
        handleClick(row) {
            console.log(row);
            if (row.radio == "") {
                this.sadioes = "1";
            } else {
                this.sadioes = "2";
            }
            this.details = row;
            this.dialog = true;
        },
        // 
        handleSelectionChange(val) {
            console.log(val);
            this.multipleSelection = val;
        },
        // 
        disableuser() {
            this.$confirm(", ?", "", {
                    confirmButtonText: "",
                    cancelButtonText: "",
                    type: "warning"
                })
                .then(() => {
                    this.$message({
                        type: "success",
                        message: "!"
                    });
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: ""
                    });
                });
        },
        //
        enableuser() {},
        // 
        deleateuser() {
            this.$confirm(", ?", "", {
                    confirmButtonText: "",
                    cancelButtonText: "",
                    type: "warning"
                })
                .then(() => {
                    this.$message({
                        type: "success",
                        message: "!"
                    });
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: ""
                    });
                });
        },
        //
        exportExcel() {
            require.ensure([], () => {
                const {
                    export_json_to_excel
                } = require("../../excel/Export2Excel");
                const tHeader = [
                    "",
                    "ID",
                    "",
                    "",
                    "",
                    ""
                ];
                // Excel
                const filterVal = [
                    "status",
                    "user_id",
                    "user_name",
                    "user_tel",
                    "flow",
                    "time_create"
                ];
                // indexnickNamenametableData
                const list = this.tableData; //datatableDatalist
                const data = this.formatJson(filterVal, list);
                export_json_to_excel(tHeader, data, "");
            });
        },
        formatJson(filterVal, jsonData) {
            return jsonData.map(v => filterVal.map(j => v[j]));
        },

        // 
        headClass() {
            return "text-align: center;background:#eef1f6;";
        },
        // 
        rowClass() {
            return "text-align: center;";
        },
        upFile() {
            let _this = this

            var file = document.getElementById('f');
            var f = file.files[0];
            if (f == undefined) {
                this.$message({
                    type: "warning",
                    message: ""
                });
                return false
            }
            this.disableStatus = true
            var totalSize = f.size;
            var len = 2 * 1024 * 1024;
            var tota_temp = Math.ceil(totalSize / len);
            var start = 0;
            var end = start + len;
            var index = 1;

            var blobSlice = File.prototype.mozSlice || File.prototype.webkitSlice || File.prototype.slice;

            var fileReader = new FileReader();

            function sliceandpost() {

                //if (start >= totalSize)return;
                //if (index >= tota_temp)return;

                var temp = f.slice(start, end);
                var formData = new FormData();
                formData.append('file', temp);
                formData.append('fileName', f.name);
                formData.append('num', index);
                formData.append('start', start);
                formData.append('end', end);
                formData.append('totalSize', totalSize);
                formData.append('total', tota_temp);
                var url = 'http://39.100.131.247/cloud/uploadsdk';
                var xhr = new XMLHttpRequest();
                xhr.onreadystatechange = onchange;
                xhr.open('POST', url);
                xhr.send(formData);

                function onchange() {
                    // 4 = "loaded"
                    if (xhr.readyState == 4) {
                        // 200 = "OK"
                        if (xhr.status == 200) {
                            //var headers =  JSON.parse(xhr.responseText);
                            var headers = JSON.parse(xhr.response);
                            console.log(headers);
                            //
                            if (headers.status == 0) {
                                index = index + 1;
                                // 
                                start = end;
                                end = start + len;
                                // totalSizetotalSizeendtotalSize
                                if (end > totalSize) {
                                    end = totalSize;
                                }
                                document.getElementById("loading").style.width = Math.floor(index / tota_temp * 100) + "%";
                                document.getElementById('per').innerHTML = Math.floor(index / tota_temp * 100)
                                sliceandpost();

                            } else if (headers.status == 1) {
                                document.getElementById('result').innerHTML = ":" + headers.msg
                                _this.sdkUrl = headers.url
                                _this.disableStatus = false

                            } else {

                            }

                        } else {
                            alert("Problem retrieving XML data:" + xhr.statusText);
                        }

                    }
                }
            }
            sliceandpost();

        },
        //
        onSubmitUpload() {
            this.dialog=false

            let param = {
                url: this.sdkUrl,
                type: this.valueActive2,
                content: this.checkList.join(","),
                version: this.versionInput,
                description: ""

            }
            console.log(param)
            savesdk(param).then(res => {
                console.log(res)
                if (res.status == 0) {
                    this.$message({
                        message: "",
                        type: "success"
                    })
                    this.querySdkList()
                }
            }).catch(error => {
                console.log(error)

            })
        },
    },

};
</script>

<style lang="scss" scoped>
.customWidth {
    width: 30% !important;
}

//
.aa {
    transition: all 1s;
}

.go {
    transform: rotate(-180deg);
    transition: all 1s;
}

.add-sdk {
    width: 80%;
    height: auto;
    margin: 0 auto;

    .item {
        width: 100%;
        display: flex;
        justify-content: flex-start;
        flex-flow: row;
        align-items: center;
        margin-top: 20px;

        .choose-file {
            width: 130px;
            height: 25px;
            position: absolute;
            padding: 2px;
            z-index: 99;

        }

        .choose-input {
            position: absolute;
            z-index: 100;
            width: auto;
            opacity: 0;
            height: auto;
        }

        .onchoose-file {
            padding: 7px 20px;
            margin-top: 35px;
        }

        .item_l {
            width: 100px;
            text-align: right;
            margin-right: 10px;

        }
    }
}
</style>



