<template>
<div class="content">
    <el-breadcrumb separator="/">
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item>
            <a href="/"></a>
        </el-breadcrumb-item>
    </el-breadcrumb>
    <div>
        <!--  -->
        <div class="seach">
            <div class="seach_top">
                <el-input placeholder="ID" v-model="input" class="input-with-select" @keyup.enter.native="onSubmit">
                    <i slot="prefix" class="el-input__icon el-icon-search" @click="seachuser()"></i>
                </el-input>
                <div class="seach_top_right" @click="option_display()">
                    
                    <i class="el-icon-caret-bottom" :class="[rotate?'fa fa-arrow-down go':'fa fa-arrow-down aa']"></i>
                </div>
            </div>
            <div v-if="optiondisplay" class="seach_bottom">
                <span></span>
                <el-select v-model="value" placeholder="">
                    <el-option v-for="(item, index) in options" :key="index" :label="item.label" :value="item.value"></el-option>
                </el-select>
                <span></span>
                <el-date-picker v-model="value1" type="datetimerange" range-separator="" start-placeholder="" end-placeholder=""></el-date-picker>
                <div class="seach_bottom_btn">
                    <el-button type="primary" plain size="mini" @click="seachuser()"></el-button>
                    <el-button plain size="mini" @click="reset()"></el-button>
                </div>
            </div>
        </div>
        <!--  -->
        <div class="con_lable">
            <div style="padding:10px;text-align: left;">
                <el-button type="primary" @click="exportExcel1()"></el-button>
                <el-button type="primary" @click="exportExcel1()"></el-button>
            </div>

            <!--  -->
            <el-table ref="multipleTable" :data="tableData" height="auto" tooltip-effect="dark" style="width: 100%" :cell-style="rowClass" :header-cell-style="headClass" :default-sort="{prop: 'date', order: 'descending'}" @selection-change="handleSelectionChange">
                <el-table-column type="selection" width="55"></el-table-column>
                <el-table-column label="" prop="user_id" width="120">

                </el-table-column>
                <el-table-column prop="user_id" label=""></el-table-column>
                <el-table-column prop="user_name" label="ID"></el-table-column>
                <el-table-column prop="time_create" sortable label=""></el-table-column>

                <el-table-column prop="user_tel" label=""></el-table-column>
                <el-table-column prop="flow" label=""></el-table-column>

            </el-table>
            <!--  -->
            <el-dialog title="" :visible.sync="dialog" custom-class="customWidth" width="40%">
                <div>
                    <el-tabs v-model="activeName" @tab-click="handleClick">
                        <el-tab-pane label="" name="first">
                            <div class="refresh">
                                <div class="item">
                                    <div style="margin-right:20px;"></div>
                                    <div>
                                        <el-radio v-model="radio" label="1"></el-radio>
                                        <el-radio v-model="radio" label="2"></el-radio>
                                    </div>
                                </div>
                                <div class="item">
                                    Filed 10  10 
                                </div>
                                <div class="item">
                                    <textarea></textarea>
                                </div>
                            </div>
                        </el-tab-pane>
                        <el-tab-pane label="" name="second">
                            <div class="refresh">
                                <div class="item">
                                    <div style="margin-right:20px;"></div>
                                    <div>
                                        <el-radio v-model="radio" label="1"></el-radio>
                                        <el-radio v-model="radio" label="2"></el-radio>
                                    </div>
                                </div>
                                <div class="item">
                                    Filed 10  10 
                                </div>
                                <div class="item">
                                    <textarea></textarea>
                                </div>
                            </div>
                        </el-tab-pane>
                    </el-tabs>
                </div>
                <div class="dialog-footer" style="margin-top:15px;text-align:center;">
                    <el-button type="primary" @click="dialog=false"> </el-button>
                </div>
            </el-dialog>

            <!--  -->
            <div style="margin-top: 20px;display: flex;justify-content: space-between;align-items: center;">
                <div>
                    <el-button @click="enableuser()" type="success"></el-button>
                    <el-button @click="disableuser()" type="warning"></el-button>
                    <el-button @click="deleateuser()" type="danger"></el-button>
                </div>
                <fenye style="float:right;margin:10px 0 0 0;" @fatherMethod="getpage" @fathernum="gettol" :pagesa="total_cnt"></fenye>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import fenye from "@/components/fenye";
import {
    userlist
} from "../../servers/api";
export default {
    data() {
        return {
            input: "", //
            value1: "",
            dialog: false,
            radio: "1",
            radioes: "1",
            activeName: "first",

            sadioes: 1,
            pagesize: 10,
            total_cnt: 1,
            form: {
                account: "",
                nickname: "",
                pwd: "",
                conpwd: "",
                actualname: "",
                tel: "",
                delivery: false,
                type: [],
                resource: "",
                desc: ""
            },
            details: {},

            formLabelWidth: "60px",
            optiondisplay: false,
            rotate: false,

            tolpage: 1,

            value: "",
            options: [{
                    value: 0,
                    label: ""
                },
                {
                    value: 1,
                    label: ""
                },
                {
                    value: 2,
                    label: ""
                },
                {
                    value: 3,
                    label: ""
                },
                {
                    value: 4,
                    label: ""
                },
                {
                    value: 5,
                    label: ""
                }
            ],
            pager: {
                count: 0,
                page: 1,
                rows: 100
            },
            tableData: [{
                    status: "",
                    user_id: "01653752",
                    user_name: "",
                    user_tel: 13501201248,
                    time_create: "2019/11/05",
                    flow: "48773416"
                },
                {
                    status: "",
                    user_id: "0264512",
                    user_name: "",
                    user_tel: 13501201248,
                    time_create: "2019/11/05",
                    flow: "4876487416"
                },
                {
                    status: "",
                    user_id: "0634712",
                    user_name: "",
                    user_tel: 13501201248,
                    time_create: "2019/11/05",
                    flow: "487416"
                },
                {
                    status: "",
                    user_id: "012",
                    user_name: "",
                    user_tel: 13501201248,
                    time_create: "2019/11/05",
                    flow: "487412476"
                },
                {
                    status: "",
                    user_id: "0161362",
                    user_name: "",
                    user_tel: 13501201248,
                    time_create: "2019/11/05",
                    flow: "154487416"
                },
                {
                    status: "",
                    user_id: "01635472",
                    user_name: "",
                    user_tel: 13501201248,
                    time_create: "2019/11/05",
                    flow: "487416"
                },
                {
                    status: "",
                    user_id: "07453712",
                    user_name: "",
                    user_tel: 13501201248,
                    time_create: "2019/11/05",
                    flow: "876487416"
                }
            ]
        };
    },
    components: {
        fenye
    },
    created() {},
    mounted() {
        this.getuserlist();
    },
    methods: {
        //
        getuserlist() {
            let params = new Object();
            params.page = this.pager.page - 1;
            params.search = this.input;
            params.status = parseInt(this.value);
            userlist(params)
                .then(res => {
                    console.log(res);
                    if (res.status != 0) {
                        this.$message({
                            message: `${res.err_msg}`,
                            type: "error"
                        });
                    } else {
                        if (res.result.page == 0) {
                            this.pager.count =
                                res.result.cols.length * (res.result.page + 1) +
                                res.result.les_count;
                        } else {
                            this.pager.count =
                                10 +
                                res.result.cols.length * res.result.page +
                                res.result.les_count;
                        }
                        let nowArr = res.result.cols;
                        for (var i = 0; i < nowArr.length; i++) {
                            nowArr[i].time_create = this.common.getTimes(
                                parseInt(nowArr[i].time_create * 1000)
                            );
                            nowArr[i].time_update = this.common.getTimes(
                                parseInt(nowArr[i].time_update * 1000)
                            );
                            if (nowArr[i].status == 0) {
                                nowArr[i].status = "";
                            } else {
                                nowArr[i].status = "";
                            }
                        }
                        // this.tableData = nowArr;
                    }
                })
                .catch(err => {
                    console.log(err);
                });
        },
        //
        getpage(pages) {
            console.log(pages);
            this.tolpage = pages;
            //this.getdata();
        },
        //
        gettol(pagetol) {
            console.log(pagetol);
            this.pagesize = pagetol;
            //this.getdata();
        },
        //
        onSubmit() {
            console.log("enter");
        },
        //
        option_display() {
            console.log("******");
            this.optiondisplay = !this.optiondisplay;
            this.rotate = !this.rotate;
        },
        //
        seachuser() {
            console.log(this.value, this.input);
            this.optiondisplay = false;
            this.value = "";
            this.input = "";
        },
        //
        reset() {
            this.value = "";
            this.input = "";
            console.log("");
        },
        //
        handleClick(row) {
            console.log(row);
            if (row.radio == "") {
                this.sadioes = "1";
            } else {
                this.sadioes = "2";
            }
            this.details = row;
            this.dialog = true;
        },
        // 
        handleSelectionChange(val) {
            console.log(val);
            this.multipleSelection = val;
        },
        // 
        disableuser() {
            this.$confirm(", ?", "", {
                    confirmButtonText: "",
                    cancelButtonText: "",
                    type: "warning"
                })
                .then(() => {
                    this.$message({
                        type: "success",
                        message: "!"
                    });
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: ""
                    });
                });
        },
        //
        enableuser() {},
        // 
        deleateuser() {
            this.$confirm(", ?", "", {
                    confirmButtonText: "",
                    cancelButtonText: "",
                    type: "warning"
                })
                .then(() => {
                    this.$message({
                        type: "success",
                        message: "!"
                    });
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: ""
                    });
                });
        },
        //
        exportExcel1() {
            this.dialog = true
        },
        formatJson(filterVal, jsonData) {
            return jsonData.map(v => filterVal.map(j => v[j]));
        },

        // 
        headClass() {
            return "text-align: center;background:#eef1f6;";
        },
        // 
        rowClass() {
            return "text-align: center;";
        }
    }
};
</script>

<style lang="scss" scoped>


.refresh {
    width: 100%;
    display: flex;
    flex-flow: column;

    .item {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        margin-top: 15px;

        textarea {
            width: 700px;
            height: 300px;
            border: 1px solid #DCDFE6;
            ;
            resize: none;

        }
    }
}


</style>



