<template>
<div class="content">
    <el-breadcrumb separator="/">
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item>
            <a href="/"></a>
        </el-breadcrumb-item>
    </el-breadcrumb>
    <section class="myself-container">
        <div class="device_form">
            <el-form ref="form" :model="form">
                <el-row type="flex">
                    <div class="search-con">
                        <i class="el-icon-search" @click="searchInfo" style="color:#606266"></i>
                        <el-input class="search-input" v-model="searchText" placeholder="" @keyup.enter.native="searchInfo"></el-input>
                    </div>
                    <div @click="getShow()" class="div_show" style="color:#606266">
                        
                        <i class="el-icon-caret-bottom" :class="[rotate?'fa fa-arrow-down go':'fa fa-arrow-down aa']"></i>
                    </div>
                </el-row>
                <el-row type="flex" class="row_activess" v-show="showState">
                    <el-form-item label="" style="display: flex;">
                        <el-select v-model="value" placeholder="">
                            <el-option v-for="item in options2" :key="item.value" :label="item.label" :value="item.value"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="searchInfo"></el-button>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="reset()"></el-button>
                    </el-form-item>
                </el-row>
            </el-form>
        </div>
        <div class="devide_table">
            <el-row type="flex" class="row_active">
                <el-col :span="6">
                    <el-button type="primary" @click="addAccout">
                        
                        <span class="el-icon-plus"></span>
                    </el-button>
                </el-col>
            </el-row>
            <el-row type="flex" class="row_active">
                <el-col :span="24">
                    <tableBarActiveAccount id="rebateSetTable" ref="table1" tooltip-effect="dark" :tableData="tableData" @handleSelectionChange="handleSelectionChange" :clomnSelection="clomnSelection" :rowHeader="rowHeader" :tableOption="tableOption" @disable="disable" @toChange="toChange" @changePassword="changePassword" @toDelete="toDelete"></tableBarActiveAccount>
                </el-col>
            </el-row>
        </div>
        <div class="devide_pageNation" style="display: flex;justify-content: space-between;">
            <el-row type="flex">
                <el-col :span="6" style="display: flex;justify-content: justify-content: flex-start;">
                    <el-button type="success" size="small" @click="allOn"></el-button>
                    <el-button type="warning" size="small" @click="allOff"></el-button>
                    <el-button type="danger" size="small" @click="allDelete"></el-button>
                </el-col>
            </el-row>
            <el-row type="flex">
                <el-col :span="6">
                    <pageNation :pager="pager" @handleSizeChange="handleSizeChange" @handleCurrentChange="handleCurrentChange"></pageNation>
                </el-col>
            </el-row>
        </div>
        <el-dialog :visible.sync="dialogVisible" width="20%">
            <div class="addaccout">
                <el-form :model="ruleForm2" ref="ruleForm2" label-position="left" class="demo-ruleForm">
                    <h3 class="title"></h3>
                    <el-form-item label=":">
                        <el-radio v-model="ruleForm2.radio" label="0"></el-radio>
                        <el-radio v-model="ruleForm2.radio" label="1"></el-radio>
                    </el-form-item>
                    <el-form-item prop="username" :rules="[
      {validator: jiousername, trigger: 'blur' }
    ]">
                        <el-form-item label=":">
                            <el-input v-model="ruleForm2.username" placeholder="4-20"></el-input>
                        </el-form-item>
                    </el-form-item>
                    <el-form-item prop="password" :rules="[
      {validator: jiopwd, trigger: 'blur' }
    ]">
                        <el-form-item label=":" placeholder="">
                            <el-input v-model="ruleForm2.password" type="password" placeholder="6-20_"></el-input>
                        </el-form-item>
                    </el-form-item>
                    <el-form-item prop="password2" :rules="[
      {validator: jioqpwd, trigger: 'blur' }
    ]">
                        <el-form-item label=":">
                            <el-input v-model="ruleForm2.password2" placeholder="" type="password"></el-input>
                        </el-form-item>
                    </el-form-item>
                    <el-form-item prop="name" :rules="[
      {validator: jioname, trigger: 'blur' }
    ]">
                        <el-form-item label=":">
                            <el-input v-model="ruleForm2.name" placeholder="4-20"></el-input>
                        </el-form-item>
                    </el-form-item>
                    <el-form-item prop="phone" :rules="[
      {validator: jiophone, trigger: 'blur' }
    ]">
                        <el-form-item label=":">
                            <el-input v-model="ruleForm2.phone" placeholder="11"></el-input>
                        </el-form-item>
                    </el-form-item>
                    <el-form-item style="width:100%;display: flex;justify-content:center;">
                        <el-button type="primary" @click.native.prevent="handleSubmit"></el-button>
                        <el-button @click="notall()"></el-button>
                    </el-form-item>
                </el-form>
            </div>
        </el-dialog>
        <el-dialog :visible.sync="dialogVisible2" width="20%">
            <div class="addaccout">
                <el-form :model="ruleForm3" ref="ruleForm3" label-position="left" class="demo-ruleForm">
                    <h3 class="title"></h3>
                    <el-form-item label=":">
                        <el-radio v-model="ruleForm3.radio" label="0"></el-radio>
                        <el-radio v-model="ruleForm3.radio" label="1"></el-radio>
                    </el-form-item>
                    <el-form-item prop="username" :rules="[
      {validator: jiousername, trigger: 'blur' }
    ]">
                        <el-form-item label=":">
                            <el-input v-model="ruleForm3.username" placeholder="4-20"></el-input>
                        </el-form-item>
                    </el-form-item>

                    <el-form-item prop="name" :rules="[
      {validator: jioname, trigger: 'blur' }
    ]">
                        <el-form-item label=":">
                            <el-input v-model="ruleForm3.name" placeholder="4-20"></el-input>
                        </el-form-item>
                    </el-form-item>
                    <el-form-item prop="phone" :rules="[
      { validator: jiophone, trigger: 'blur' }
    ]">
                        <el-form-item label=":">
                            <el-input v-model="ruleForm3.phone" placeholder="11"></el-input>
                        </el-form-item>
                    </el-form-item>
                    <el-form-item style="width:100%;display: flex;justify-content:center;">
                        <el-button type="primary" @click.native.prevent="handleSubmit4"></el-button>
                        <el-button @click="nohandleSubmit4()"></el-button>
                    </el-form-item>
                </el-form>
            </div>
        </el-dialog>
        <el-dialog :visible.sync="dialogVisible3" width="20%">
            <div class="addaccout">
                <el-form :model="ruleForm4" status-icon :rules="rules4" ref="ruleForm4" label-position="left" class="demo-ruleForm">
                    <h3 class="title"></h3>
                    <el-form-item prop="username">
                        <el-form-item label=":">
                            <el-input v-model="ruleForm3.username" :disabled="true"></el-input>
                        </el-form-item>
                    </el-form-item>
                    <el-form-item prop="password">
                        <el-form-item label=":">
                            <el-input v-model="ruleForm4.password" placeholder="6-20_" type="password" autocomplete="off"></el-input>
                        </el-form-item>
                    </el-form-item>
                    <el-form-item prop="password2">
                        <el-form-item label=":">
                            <el-input v-model="ruleForm4.password2" placeholder="" type="password" autocomplete="off"></el-input>
                        </el-form-item>
                    </el-form-item>

                    <el-form-item style="width:100%;display: flex;justify-content:center;">
                        <el-button type="primary" @click.native.prevent="handleSubmit5"></el-button>
                        <el-button @click="nohandleSubmit5()"></el-button>
                    </el-form-item>
                </el-form>
            </div>
        </el-dialog>
    </section>
</div>
</template>

<script>
import tableBarActiveAccount from "../../comm/tableBarActiveAccount";

import pageNation from "../../comm/pageNation";
import {
    userlist,
    userinsert,
    userupdate,
    userdelete,
    userctrl
} from "../../servers/api";
import common from "../../comm/js/util.js";
export default {
    data() {
        var validatePass = (rule, value, callback) => {
            if (value === "") {
                callback(new Error(""));
            } else {
                var fsdf = /^(?![\d]+$)(?![a-zA-Z]+$)(?![^\da-zA-Z]+$).{6,20}$/;
                if (fsdf.test(value) === false) {
                    callback(new Error(""));
                } else {
                    if (this.ruleForm4.password2 !== "") {
                        this.$refs.ruleForm4.validateField("password2");
                    }
                    callback();
                }
            }
        };
        var validatePass2 = (rule, value, callback) => {
            if (value === "") {
                callback(new Error(""));
            } else if (value !== this.ruleForm4.password) {
                callback(new Error("!"));
            } else {
                callback();
            }
        };
        return {
            form: {},
            dialogVisible: false,
            rotate: false,
            dialogVisible2: false,
            dialogVisible3: false,
            searchText: "",
            placeholder: "",
            operatingStatus: true,
            clomnSelection: true,
            reserveselection: true,
            value1: "",
            value2: "",
            value: "",
            options2: [{
                    value: "2",
                    label: ""
                },
                {
                    value: "0",
                    label: ""
                },
                {
                    value: "1",
                    label: ""
                },

            ],
            ruleForm2: {
                username: "",
                nickname: "",
                password: "",
                password2: "",
                value: "",
                radio: "0",
                name: "",
                phone: ""
            },
            ruleForm3: {
                username: "",
                nickname: "",
                password: "",
                password2: "",
                value: "",
                radio: "0",
                name: "",
                phone: "",
                id: ""
            },
            ruleForm4: {
                password: "",
                password2: ""
            },
            //
            rules4: {
                password: [{
                    validator: validatePass,
                    trigger: "blur"
                }],
                password2: [{
                    validator: validatePass2,
                    trigger: "blur"
                }]
            },
            rowHeader: [{
                    prop: "status",
                    label: ""
                },
                {
                    prop: "username",
                    label: ""
                },
                {
                    prop: "name",
                    label: "",

                },
                {
                    prop: "user_update",
                    label: "",

                },
                {
                    prop: "time_update",
                    label: ""
                }
            ],
            tableData: [],
            tableOption: {
                label: "",
                width: "400px",
                options: [{
                        label: "",
                        type: "primary",
                        methods: "checkInfo"
                    },

                    {
                        label: "",
                        type: "danger",
                        methods: "password"
                    },
                    {
                        label: "",
                        type: "danger",
                        methods: "disable"
                    },
                    {
                        label: "",
                        type: "danger",
                        methods: "delete"
                    }
                ]
            },
            pager: {
                count: 0,
                page: 1,
                rows: 100
            },
            showState: false,
            allId: []
        };
    },
    mounted: function () {
        this.queryUserList();
    },
    methods: {
        //
        reset() {
            this.value = "";
            this.searchText = "";
            this.queryUserList();
        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
            console.log(val);
            this.allId = [];
            for (var i = 0; i < val.length; i++) {
                this.allId.push(val[i].id);
            }
            console.log(this.allId);
        },
        //
        allDisable() {
            if (this.allId.length <= 0) {
                this.$message({
                    message: "",
                    type: "error"
                });
                return false;
            }
            let param = new Object();
            param.ids = this.allId.join(",");
            param.type = 0;
            userctrl(param)
                .then(res => {
                    if (res.status == 0) {
                        this.$message({
                            message: "",
                            type: "success"
                        });
                        this.queryUserList();
                    } else {
                        this.$message({
                            message: `${res.status.msg}`,
                            type: "error"
                        });

                    }
                })
                .catch(error => {});
        },
        //
        allOn() {
            if (this.allId.length <= 0) {
                this.$message({
                    message: "",
                    type: "error"
                });
                return false;
            }
            let param = new Object();
            param.ids = this.allId.join(",");
            param.type = 0;
            userctrl(param)
                .then(res => {
                    if (res.status == 0) {
                        this.$message({
                            message: "",
                            type: "success"
                        });
                        this.queryUserList();
                    } else {
                        this.$message({
                            message: `${res.status.msg}`,
                            type: "error"
                        });

                    }
                })
                .catch(error => {});
        },
        //
        allOff() {
            if (this.allId.length <= 0) {
                this.$message({
                    message: "",
                    type: "error"
                });
                return false;
            }
            let param = new Object();
            param.ids = this.allId.join(",");
            param.type = 1;
            userctrl(param)
                .then(res => {
                    if (res.status == 0) {
                        this.$message({
                            message: "",
                            type: "success"
                        });
                        this.queryUserList();
                    } else {
                        this.$message({
                            message: `${res.status.msg}`,
                            type: "error"
                        });

                    }
                })
                .catch(error => {});
        },
        //
        allDelete() {
            if (this.allId.length <= 0) {
                this.$message({
                    message: "",
                    type: "error"
                });
                return false;
            }
            let param = new Object();
            param.ids = this.allId.join(",");
            param.type = 2;
            userctrl(param)
                .then(res => {
                    if (res.status == 0) {
                        this.$message({
                            message: "",
                            type: "success"
                        });
                        this.queryUserList();
                    } else {
                        this.$message({
                            message: `${res.status.msg}`,
                            type: "error"
                        });

                    }
                })
                .catch(error => {});
        },

        queryUserList() {
            let param = new Object();
            param.page = this.pager.page - 1;
            param.search = this.searchText;
            param.status = this.value == "2" ? null : parseInt(this.value);
            console.log(param);
            userlist(param)
                .then(res => {
                    console.log(res);
                    if (res.status != 0) {
                        this.$message({
                            message: `${res.err_msg}`,
                            type: "error"
                        });
                    } else {
                        if (res.result.page == 0) {
                            this.pager.count =
                                res.result.cols.length * (res.result.page + 1) +
                                res.result.les_count;
                        } else {
                            this.pager.count =
                                10 +
                                res.result.cols.length * res.result.page +
                                res.result.les_count;
                        }
                        let nowArr = res.result.cols;
                        for (var i = 0; i < nowArr.length; i++) {
                            nowArr[i].time_create = this.common.getTimes(
                                parseInt(nowArr[i].time_create * 1000)
                            );
                            nowArr[i].time_update = this.common.getTimes(
                                parseInt(nowArr[i].time_update * 1000)
                            );
                            if (nowArr[i].status == 0) {
                                nowArr[i].status = "";
                            } else {
                                nowArr[i].status = "";
                            }
                        }
                        this.tableData = nowArr;
                    }
                })
                .catch(error => {
                    console.log(error);
                });
        },
        //
        searchInfo() {
            this.showState = false;
            this.queryUserList();
        },
        getShow() {
            this.showState = !this.showState;
            this.rotate = !this.rotate;
        },
        //
        handleSizeChange() {},
        //
        handleCurrentChange(val) {
            this.pager.page = val.val;
            this.queryUserList();
        },
        //
        handleSubmit(ev) {
            //return false
            var _this = this;
            this.$refs.ruleForm2.validate(valid => {
                if (valid) {
                    var loginParams = {
                        username: this.ruleForm2.username,
                        nickname: "aa",
                        password: this.ruleForm2.password,
                        password2: this.ruleForm2.password2,
                        role_id: 0,
                        phone: this.ruleForm2.phone,
                        status: parseInt(this.ruleForm2.radio),
                        name: this.ruleForm2.name
                    };
                    userinsert(loginParams).then(data => {
                        this.dialogVisible = false;
                        let {
                            msg,
                            status,
                            user
                        } = data;
                        if (status !== 0) {
                            this.$message({
                                message: msg,
                                type: "error"
                            });
                        } else {
                            this.$message({
                                message: "",
                                type: "success"
                            });
                            this.queryUserList();
                        }
                    });
                } else {
                    console.log("error submit!!");
                    return false;
                }
            });
        },
        //
        notall() {
            this.dialogVisible = false;
            this.ruleForm2.username = "";
            this.ruleForm2.password = "";
            this.ruleForm2.password2 = "";
            this.ruleForm2.phone = "";
            this.ruleForm2.name = "";
        },
        //
        disable(val) {
            this.ruleForm3.username = val.username;
            this.ruleForm3.id = val.id;
            this.ruleForm3.name = val.name;
            this.ruleForm3.phone = val.phone;
            (this.ruleForm3.nickname = "aa"),
            (this.ruleForm3.password = ""),
            (this.ruleForm3.password2 = ""),
            (this.ruleForm3.role_id = 0);
            if (val.status == "") {
                this.ruleForm3.status = 1;
            } else {
                this.ruleForm3.status = 0;
            }
            let param = new Object();
            param = this.ruleForm3;

            this.$confirm("", "", {
                    type: "warning"
                })
                .then(() => {
                    userupdate(param).then(data => {
                        this.dialogVisible2 = false;
                        let {
                            msg,
                            status,
                            user
                        } = data;
                        if (status !== 0) {
                            this.$message({
                                message: msg,
                                type: "error"
                            });
                        } else {
                            this.$message({
                                message: "",
                                type: "success"
                            });
                            this.queryUserList();
                        }
                    });
                })
                .catch(() => {});
        },
        //
        toChange(val) {
            console.log(val);
            this.dialogVisible2 = true;
            this.ruleForm3.username = val.username;
            this.ruleForm3.id = val.id;
            this.ruleForm3.name = val.name;
            this.ruleForm3.phone = val.phone;
            (this.ruleForm3.nickname = "aa"),
            (this.ruleForm3.password = ""),
            (this.ruleForm3.password2 = ""),
            (this.ruleForm3.role_id = 0);

            //this.ruleForm3.status = parseInt(this.ruleForm3.radio)
        },
        //
        changePassword(val) {
            this.dialogVisible3 = true;
            this.ruleForm3.username = val.username;
            this.ruleForm3.id = val.id;
            this.ruleForm3.name = val.name;
            this.ruleForm3.phone = val.phone;
            (this.ruleForm3.nickname = "aa"), (this.ruleForm3.role_id = 0);
        },
        //
        toDelete(val) {
            let param = new Object();
            param.id = val.id;
            this.$confirm("?", "", {
                    type: "warning"
                })
                .then(() => {
                    userdelete(param)
                        .then(res => {
                            if (status != 0) {
                                this.$message({
                                    message: msg,
                                    type: "error"
                                });
                            } else {
                                this.$message({
                                    message: "",
                                    type: "success"
                                });
                                this.queryUserList();
                            }
                        })
                        .catch(error => {});
                })
                .catch(() => {});
        },
        addAccout() {
            this.dialogVisible = true;
        },
        //
        handleSubmit4(ev) {
            //return false
            var _this = this;
            this.$refs.ruleForm3.validate(valid => {
                if (valid) {
                    var loginParams = this.ruleForm3;
                    this.ruleForm3.status = parseInt(this.ruleForm3.radio);
                    userupdate(loginParams).then(data => {
                        this.dialogVisible2 = false;
                        let {
                            msg,
                            status,
                            user
                        } = data;
                        if (status !== 0) {
                            this.$message({
                                message: msg,
                                type: "error"
                            });
                        } else {
                            this.$message({
                                message: "",
                                type: "success"
                            });
                            this.queryUserList();
                        }
                    });
                } else {
                    console.log("error submit!!");
                    return false;
                }
            });
        },
        //
        nohandleSubmit4() {
            this.dialogVisible2 = false;
        },
        //
        handleSubmit5(ev) {
            var _this = this;
            this.$refs.ruleForm4.validate(valid => {
                if (valid) {
                    var loginParams = this.ruleForm3;
                    this.ruleForm3.password = this.ruleForm4.password;
                    this.ruleForm3.password2 = this.ruleForm4.password2;
                    this.ruleForm3.status = parseInt(this.ruleForm3.radio);

                    userupdate(loginParams).then(data => {
                        let {
                            msg,
                            status,
                            user
                        } = data;
                        if (status !== 0) {
                            this.$message({
                                message: msg,
                                type: "error"
                            });
                        } else {
                            this.dialogVisible3 = false;
                            this.$message({
                                message: "",
                                type: "success"
                            });
                            this.queryUserList();
                        }
                    });
                } else {
                    console.log("error submit!!");
                    return false;
                }
            });
        },
        //
        nohandleSubmit5() {
            this.dialogVisible3 = false;
        },
        //
        jiousername(rule, value, callback) {
            if (value === "") {
                callback(new Error(""));
            } else {
                var fsdusername = /^[a-zA-Z][a-zA-Z0-9]{3,20}$/;
                if (fsdusername.test(value) === false) {
                    callback(new Error(""));
                } else {
                    callback();
                }
            }
        },
        //
        jioname(rule, value, callback) {
            if (value === "") {
                callback(new Error(""));
            } else {
                var fsdname = /^[\u4E00-\u9FA5A-Za-z0-9]{4,20}$/;
                if (fsdname.test(value) === false) {
                    callback(new Error(""));
                } else {
                    callback();
                }
            }
        },
        //
        jiophone(rule, value, callback) {
            if (value === "") {
                callback(new Error(""));
            } else {
                var fsdtel = /^[1]([3-9])[0-9]{9}$/;
                console.log(value, fsdtel.test(value));
                if (fsdtel.test(value) === false) {
                    callback(new Error(""));
                } else {
                    callback();
                }
            }
        },
        //
        jiopwd(rule, value, callback) {
            let fsdfpwd = /^(?![\d]+$)(?![a-zA-Z]+$)(?![^\da-zA-Z]+$).{6,20}$/;
            if (fsdfpwd.test(value) === false) {
                callback(new Error(""));
            } else {
                if (this.ruleForm2.password2 !== "") {
                    this.$refs.ruleForm2.validateField("password2");
                }
                callback();
            }
        },
        //
        jioqpwd(rule, value, callback) {
            if (value === "") {
                callback(new Error(""));
            } else if (value !== this.ruleForm2.password) {
                callback(new Error("!"));
            } else {
                callback();
            }
        }
    },
    components: {
        pageNation: pageNation,
        tableBarActiveAccount: tableBarActiveAccount
    }
};
</script>

<style lang="less">
.search-con {
    width: 250px;
    height: 40px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    border-bottom: 1px solid #999999;

    i {}

    .search-input {
        .el-input__inner {
            border: none;
            background: none;
        }
    }
}

.myself-container {
    width: 100%;
    min-width: 1600px;
    text-align: left;

    .devide_title {
        width: 100%;
        height: auto;
        overflow: hidden;
        margin-top: 10px;
        background: #f2f2f2;
        padding: 15px 0px;
        box-sizing: border-box;

        .el-col {
            padding: 0px 20px;
        }
    }

    .device_form {
        width: 100%;
        height: auto;
        overflow: hidden;
        margin-top: 20px;
        background: #ffffff;
        padding: 15px 30px;
        box-sizing: border-box;

        .el-form-item__label {
            white-space: nowrap;
        }

        .el-form-item {
            margin-bottom: 0px;
            margin-left: 10px;
        }

        .row_activess {
            margin-top: 20px;
            display: flex;
            justify-content: flex-start;
        }

        .div_show {
            width: auto;
            display: flex;
            height: 40px;
            justify-content: center;
            align-items: center;
            color: #409eff;
            cursor: pointer;
            margin-left: 20px;
        }
    }

    .devide_table {
        width: 100%;
        height: auto;
        overflow: hidden;
        margin-top: 20px;

        .el-table td,
        .el-table th {
            padding: 6px 0px;
        }

        .row_active {
            margin-top: 10px;
        }
    }

    .devide_pageNation {
        width: 100%;
        height: auto;
        overflow: hidden;
        margin-top: 20px;

        .devide_pageNation_active {
            float: right;
        }
    }
}

.addaccout {
    .title {
        width: 100%;
        text-align: center;
    }

    .el-dialog__body {
        padding: 0px;
    }

    .el-form-item {
        display: flex;
    }

    .el-form--label-left .el-form-item__label {
        text-align: right;
        width: 90px;
    }

    .el-form-item__error {
        margin-left: 80px;
    }
}

//
.aa {
    transition: all 1s;
}

.go {
    transform: rotate(-180deg);
    transition: all 1s;
}
</style>



